.profile__title {
  padding: 0;
  margin: 0;
  /* width: 90%; */
  max-width: 312px;

  font-family: "Inter", "Arial", sans-serif;
  font-weight: 500;
  font-size: 42px;
  line-height: 48px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  min-width: 140px;
}

@media screen and (max-width: 1000px) {
  .profile__title {
    position: relative;
    margin: 0 auto;
    font-size: 27px;
    line-height: 33px;
    text-align: center;
    max-width: 189px;
  }
}